 
 .item-section {
   display: block;
   border-top: 5px solid #969696;
   /* background-color: #76db95; */
   line-height: 30px;
   padding: 16px 6px;
   height: 85px;
   font-size: 16px;
   color: #000000;
   /* box-shadow: 1px 1px 13px 1px rgb(91 109 230 / 94%); */
   margin-bottom: 13px;
   clear: both;
 }    
 .item-section .item {
   float: left;
   background: #a285e0;
   padding: 5px 10px;
   border: 2px solid #ff00004d;
   border-radius: 7px;
   font-weight: 600;
   margin-right: 5px;
 }      
 .item-section .price {
   float: right;
   background: #a285e0;
   padding: 2px 15px 0px 13px;
   border: 2px solid #ff00004d;
   border-radius: 7px;
   line-height: 31px;
   font-weight: 600;
 }
 
 